@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

h1{
    letter-spacing: 3px;
    text-align: center;
    padding: 2px;
}

p{
    margin: 0.5rem;
    letter-spacing: 2px;
    font-weight: bold;
    color: red;
}

.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
}

.content{
    display: grid;
    grid-template-columns: 1fr 1fr auto;
    margin: 1rem auto;
    min-height: auto;
}

.btn{
    padding: 5px;
    background: transparent;
    color: blueviolet;
    border: none;
    /* outline: 1px solid violet; */
    box-shadow: 0 0 0 1px violet;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.2s ease;
    font-weight: bolder;
}

.btn:hover{
    box-shadow: 0 0 3px 2px blueviolet;
}


@media screen and (max-width:650px) {
    .content{
        grid-template-columns: 1fr 1fr;
    }
}
.div-component{
    display: flex;
    justify-content: center;
    width: 15rem;
    height: 5rem;
    align-items: center;
    position: relative;
    margin: 1rem;
    word-wrap: break-word;
}

.div-number{
    position: absolute;
    top: 0;
    left: 0;
    padding: 6px;
    background: #fff;
    outline: 0;
     box-shadow: 0 0 0 1px greenyellow;
    color: black;
    border-bottom-right-radius: 10px;
}

.div-color{
    text-align: center;
    letter-spacing: 1px;
    max-width: 90%;
}

@media screen and (max-width:800px) {
    .div-component{
        width: 12rem;
    }
}

@media screen and (max-width:450px) {
    .div-component{
        width:9rem;
    }
    .div-number{
        padding: 3px;
    }
}

